export default {
	data() {
		return {
			show: false,
			animationDelay: false
		};
	},
	props: {
		type: {
			type: String,
			default: 'modal'
		},
		once: {
			type: Boolean,
			default: false
		},
		maskForce:{
			type :Boolean,
			default:false
		},
		z: {
			type: Number,
			default: 9999
		},
		round:{
			type:Boolean,
			default:false
		}
	},
	computed: {
		_show() {
			if (this.once) return `${this.show ? '' : 'display:none'};z-index:${this.z};`;
			else return `z-index:${this.z};`;
		}
	},
	methods: {
		showModal(e) {
			this.show = true;
		},
		hideModal(e) {
			this.$emit('hide');
			this.animationDelay = true;
		},
		maskHide(){
			if(this.maskForce) return false;
			this.hideModal();
		},
		remove() {
			if (this.animationDelay) {
				this.show = false;
				this.$nextTick(() => {
					this.animationDelay = false;
				});
			}
		},
		afterShow(e) {
			if (this.show && !this.animationDelay) this.$emit('show');
		},
		handle() {}
	}
};
